import { formatInTimeZone } from "date-fns-tz"
import { USER_TIMEZONE } from "@rp/common/utils"
import { BriefConfig } from "@rp/react/tanstack-api/brief"
import { formatDaysToText } from "./scheduleDays.utils"

const getBriefReportNotGeneratedText = (
  briefConfig: BriefConfig | null,
): {
  daysText: string
  timeText: string
  preposition: string
} => {
  const hasScheduleInformation = !!briefConfig?.schedule.days
  const daysText = hasScheduleInformation ? formatDaysToText(briefConfig?.schedule.days) : ""
  const preposition = daysText === "Daily" ? "" : `on `

  // TODO: fix time when we start using real times, for now run at 07:00AM, EST
  const runTime = new Date("2025-01-01T07:00:00.000-04:00")

  const timeText = formatInTimeZone(runTime, USER_TIMEZONE.value, "H:mm")

  return { daysText, timeText, preposition }
}

export { getBriefReportNotGeneratedText }
